@import url("https://fonts.googleapis.com/icon?family=Material+Icons");
@import url("https://fonts.googleapis.com/css2?family=Azeret+Mono:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap");
/*--------------
  $breakpoint
--------------*/
/*--------------
  $color
--------------*/
/*--------------
  $base
--------------*/
body {
  background-color: #202020;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

@media (max-width: 767px) {
  .py-8 {
    padding-top: 40px !important;
    padding-bottom: 40px !important;
  }
}

.navbar::after, .navbar::before {
  content: "";
  width: 50%;
  height: 2px;
  position: absolute;
  bottom: 0;
}

.navbar::after {
  background-color: #62DB54;
}

.navbar::before {
  background-color: #09ACF5;
  right: 0;
}

.navbar-brand {
  height: 42px;
  width: 170px;
  background: url("../images/logo.svg") no-repeat center;
}

@media (max-width: 767px) {
  .navbar-brand {
    background: url("../images/logo2.svg") no-repeat left;
  }
}

.navbar-toggler {
  border: none;
}

.navbar-toggler:focus {
  box-shadow: none;
}

@media (max-width: 992px) {
  .navbar .navbar-nav {
    align-items: center;
  }
}

.navbar .nav-link:hover {
  color: #62DB54 !important;
}

.banner {
  background: url("../images/bg_home.jpg") no-repeat center;
  background-size: cover;
  height: calc(100vh - 90px);
}

@media (max-width: 1400px) {
  .banner {
    height: 680px;
  }
}

@media (max-width: 767px) {
  .banner {
    background-image: url("../images/bg_home_mobile.jpg");
    height: 500px;
  }
}

@media (max-width: 576px) {
  .banner {
    height: 336px;
  }
}

.banner h2 {
  font-size: 3rem;
  line-height: 70px;
  font-weight: bold;
}

@media (max-width: 767px) {
  .banner h2 {
    font-size: 1.5rem;
    line-height: 35px;
    font-weight: bold;
  }
}

.banner h2::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}

.banner h2::before {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 100%;
  transform: translate(-50%, -50%);
}

.banner h2 span {
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(to top, #62DB54, #09ACF5);
}

.banner p {
  font-size: 18px;
  line-height: 26px;
}

@media (max-width: 767px) {
  .banner p {
    font-size: 14px;
    line-height: 20px;
  }
}

.banner p::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 100%;
  left: 0;
  transform: translate(-50%, -50%);
}

.banner p::before {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fff;
  position: absolute;
  top: 100%;
  left: 100%;
  transform: translate(-50%, -50%);
}

.title {
  position: relative;
  font-size: 1.75rem;
  line-height: 2.5rem;
  font-weight: bold;
  color: #fff;
  text-align: center;
  margin: 0;
}

@media (max-width: 767px) {
  .title {
    font-size: 1.5rem;
    line-height: 35px;
    font-weight: bold;
  }
}

.title::before {
  content: "";
  width: 50px;
  height: 2px;
  background-color: #62DB54;
  bottom: -16px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: 992px) {
  .introduce .title::before {
    left: 0;
    transform: translate(0);
  }
}

.introduce-card {
  transition: all .3s ease-in;
  border-radius: 12px;
  border: 1px solid transparent;
  cursor: pointer;
}

.introduce-card:hover {
  background-color: #202020;
  border: 1px solid #62DB54;
  color: #fff !important;
}

.introduce-card:hover span {
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(to top, #62DB54, #09ACF5);
}

@media (max-width: 767px) {
  .introduce-card {
    background-color: #202020;
    color: #fff !important;
  }
  .introduce-card span {
    color: transparent;
    background-clip: text;
    background-image: linear-gradient(to top, #62DB54, #09ACF5);
  }
}

.introduce span {
  font-size: 50px;
  line-height: 50px;
}

.class-cardA::after {
  content: "01";
}

.class-cardB::after {
  content: "02";
}

.class-cardC::after {
  content: "03";
}

.class-cardD::after {
  content: "04";
}

.class-card {
  position: relative;
  border-radius: 12px;
}

.class-card::after {
  font-size: 3rem;
  line-height: 3.5rem;
  font-weight: bold;
  color: #62DB54;
  background-color: #202020;
  padding: 0 8px;
  position: absolute;
  left: 40px;
  top: 0;
  transform: translateY(-50%);
}

.class-card h4 {
  font-size: 1.5rem;
  line-height: 2.25rem;
  font-weight: bold;
}

.class-card p {
  font-size: 18px;
  line-height: 26px;
}

.program p {
  display: inline-block;
  border-radius: 8px;
  cursor: pointer;
}

.program .swiper-slide {
  width: max-content !important;
}

.course-card {
  border-radius: 24px;
  transition: all .3s ease-in;
  outline: 2px solid transparent;
}

.course-card:hover {
  outline: 2px solid #62DB54;
}

.course-card p {
  font-size: 1rem;
  line-height: 1.5rem;
  text-align: justify;
}

.course-card h4 {
  min-height: 48px;
}

.course-card h4, .course-card p {
  overflow: hidden;
  display: -webkit-box;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  white-space: normal;
}

.course img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
}

.course-price::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  bottom: 70px;
}

.course-btn {
  color: #fff;
  background-image: linear-gradient(to left, #62DB54, #09ACF5);
  display: inline-block;
  padding: 8px 32px;
  font-size: 18px;
  line-height: 26px;
  border-radius: 8px;
  transition: all .2s ease-in;
}

.course-btn:hover {
  color: #fff;
  font-weight: bold;
  transform: scale(1.1);
}

.learn-card {
  cursor: pointer;
}

.learn-card:hover span {
  color: #62DB54;
}

.learn-card:hover .title:before {
  background-color: #62DB54;
  width: 70px;
}

.learn-card:hover p {
  color: #fff;
}

.learn-card:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #202020;
  position: absolute;
  opacity: 0.9;
  transition: all .3s ease-in;
}

@media (max-width: 767px) {
  .learn-card:before {
    opacity: 0.2;
  }
}

.learn-card:hover:before {
  opacity: 0.2;
}

.learn img {
  object-fit: cover;
  height: 615px;
  width: 100%;
}

@media (max-width: 1200px) {
  .learn img {
    height: 380px;
  }
}

.learn p {
  font-size: 18px;
  line-height: 26px;
}

@media (max-width: 767px) {
  .learn p {
    color: #fff;
  }
}

@media (max-width: 767px) {
  .learn span {
    color: #62DB54;
  }
}

.learn .title:before {
  background-color: #fff;
  transition: all .2s ease-in;
}

@media (min-width: 1200px) {
  .learn .title:before {
    left: 0;
    transform: translateY(0);
  }
}

@media (max-width: 767px) {
  .learn .title:before {
    background-color: #62DB54;
    width: 70px;
  }
}

.share-card {
  cursor: pointer;
}

.share-card:hover .share-box {
  opacity: 1;
}

.share h4 {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
}

.share p {
  font-size: 1rem;
  line-height: 1.5rem;
}

.share a {
  font-size: 18px;
  line-height: 21px;
}

.share-box {
  width: 32px;
  height: 32px;
  background-color: transparent;
  position: absolute;
  transition: all .2s ease-in;
  opacity: 0;
}

@media (max-width: 767px) {
  .share-box {
    display: none;
  }
}

.share-boxA {
  top: 0;
  left: 0;
  border-top-left-radius: 12px;
  border-top: 2px solid #62DB54;
  border-left: 2px solid #62DB54;
}

.share-boxB {
  top: 0;
  right: 0;
  border-top-right-radius: 12px;
  border-top: 2px solid #62DB54;
  border-right: 2px solid #62DB54;
}

.share-boxC {
  bottom: 0;
  left: 0;
  border-bottom-left-radius: 12px;
  border-bottom: 2px solid #62DB54;
  border-left: 2px solid #62DB54;
}

.share-boxD {
  bottom: 0;
  right: 0;
  border-bottom-right-radius: 12px;
  border-bottom: 2px solid #62DB54;
  border-right: 2px solid #62DB54;
}

.join {
  background-image: url("../images/bg_share.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.join-box {
  position: relative;
  margin: 0 auto;
  max-width: 925px;
  padding-bottom: 3rem;
}

@media (max-width: 1200px) {
  .join-box {
    margin-left: 20px;
  }
}

@media (max-width: 767px) {
  .join-box {
    flex-direction: column;
    align-items: center;
    padding-bottom: 0;
  }
}

.join-img {
  max-width: 525px;
  position: relative;
}

.join-img::after {
  content: "";
  border: 1px solid #fff;
  border-radius: 24px;
  border-bottom-right-radius: 0px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 1rem;
  left: -1rem;
  z-index: -1;
}

@media (max-width: 767px) {
  .join-img::after {
    top: 0.5rem;
    left: -0.5rem;
    border-bottom-right-radius: 24px;
  }
}

.join-img img {
  border-radius: 24px;
}

.join-message {
  position: absolute;
  right: 10px;
  bottom: 0;
  background-color: #fff;
  padding: 2rem;
  max-width: 416px;
  border-radius: 24px;
  border-bottom-left-radius: 0;
}

@media (max-width: 767px) {
  .join-message {
    position: relative;
    margin-top: 65px;
    padding: 1.5rem;
  }
}

.join-message img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: -50px;
  right: 32px;
}

@media (max-width: 767px) {
  .join-message img {
    width: 80px;
    height: 80px;
  }
}

.join-btn {
  width: 525px;
  align-items: center;
  justify-content: end;
  position: absolute;
  bottom: -16px;
  left: 0;
}

@media (max-width: 1400px) {
  .join-btn {
    justify-content: center;
  }
}

@media (max-width: 992px) {
  .join-btn {
    justify-content: start;
  }
}

@media (max-width: 767px) {
  .join-btn {
    position: relative;
    justify-content: center;
    width: auto;
  }
}

.join-pagination {
  width: auto !important;
  padding: 0 16px;
}

.join .swiper-button-prev, .join .swiper-button-next {
  position: relative;
  margin-top: 0;
  left: 0;
}

.join .swiper-button-prev::after, .join .swiper-button-next::after {
  content: "";
}

.join .swiper-pagination-bullet {
  background-color: #62DB54;
  opacity: 1;
}

.join .swiper-pagination-bullet-active {
  width: 16px;
  border-radius: 10px;
}

.reserve {
  border-radius: 24px;
}

.reserve .material-icons {
  font-size: 5rem;
  line-height: 5rem;
}

.reserve h3 {
  font-size: 1.75rem;
  line-height: 2.5rem;
  font-weight: bold;
}

.reserve h3 span {
  font-size: 18px;
  line-height: 26px;
  font-weight: normal;
}

@media (max-width: 992px) {
  .reserve h3 {
    padding: 1.5rem 0;
    text-align: center;
  }
}

.reserve a:hover {
  background-image: linear-gradient(to top, #62DB54, #09ACF5);
}

@media (max-width: 767px) {
  .reserve a {
    background-image: linear-gradient(to top, #62DB54, #09ACF5);
    border: none !important;
  }
}
